.replica-matrix {
  border-collapse: collapse;
}

thead td {
  text-align: center;
  cursor: pointer;
}

.replica-matrix td, .replica-matrix th {
  border-left: solid 1px lightgrey;
  border-right: solid 1px lightgrey;
}

.replica-matrix td, th {
  padding: 4px;
}

.replica-matrix tbody td {
  text-align: center;
}

.replica-matrix thead {
  border-bottom: 1px solid black;
}

.matrix-cell {
  text-align: right;
}

.leader-and-follower {
  font-size: small;
  color: grey;
}


